@import url(https://fonts.googleapis.com/css2?family=Dosis:wght@700&family=Readex+Pro&family=Roboto:wght@400;700&display=swap);
*{box-sizing:border-box}body,p,li,ul{margin:0}li{list-style:none}html{font-family:"Readex Pro", sans-serif}body{background-color:#626367;color:#626367}.App__container{margin:0 auto;padding:0 60px;max-width:1200px}.App__title{margin:0 auto;padding:20px 0;width:-webkit-max-content;width:max-content;color:#fff}

.UserInfo__name{color:#0d6b6f}.UserInfo__email{color:#a5acbd}

.todoItem{display:inline-block;padding:20px;border-radius:10px;background-color:#fff;box-shadow:0 10px 10px rgba(0,0,0,0.1)}.todoItem__task-status{color:#cf3f34;text-align:end}.todoItem__task-status--completed{color:#839d83}

.TodoList__list{display:flex;flex-direction:row;flex-wrap:wrap;justify-content:space-between;grid-gap:16px;gap:16px;margin:0;padding:0;list-style:none}

